.landing-page .heading-advertise {
  height: 10vh;
  background-color: white;
  margin-left: 30%;
  margin-right: 30%; }

.landing-page__container {
  margin-top: 30px;
  width: 100%; }
  .landing-page__container .welcome-text {
    font-size: 4.5rem;
    font-weight: 300;
    line-height: 1.2;
    padding-left: 1em; }
  .landing-page__container .advertise {
    background-color: white; }
  .landing-page__container .lead {
    padding-left: 4em; }
  .landing-page__container .card-margin {
    margin-left: 1.5em;
    margin-bottom: 1.5em; }
  .landing-page__container .card-title {
    font-weight: 600;
    font-size: 20px; }

@media only screen and (max-width: 500px) {
  .landing-page .heading-advertise {
    height: 10vh;
    background-color: white;
    margin-left: 30%;
    margin-right: 30%; }
  .landing-page__container {
    margin-top: 30px;
    width: 100%; }
    .landing-page__container .welcome-text {
      font-size: 3.5rem;
      font-weight: 300;
      line-height: 1.2;
      padding-left: 0.2em;
      text-align: center; }
    .landing-page__container .advertise {
      background-color: white; }
    .landing-page__container .lead {
      font-size: 1rem;
      padding-left: 2em; }
    .landing-page__container .card-margin {
      margin-left: 1.5em;
      margin-bottom: 1.5em; }
    .landing-page__container .card-title {
      font-weight: 600;
      font-size: 20px; } }
