.blog-list .heading-advertise {
  height: 10vh;
  background-color: white;
  margin-left: 30%;
  margin-right: 30%; }

.blog-list__container {
  margin-top: 30px; }
  .blog-list__container .heading-text {
    font-size: 3rem;
    font-weight: 300;
    line-height: 1.2;
    padding-left: 1.5em; }
  .blog-list__container .advertise {
    background-color: white; }
  .blog-list__container .lead {
    padding-left: 4em; }
  .blog-list__container .card-margin {
    margin-left: 1.5em;
    margin-bottom: 1.5em; }
    .blog-list__container .card-margin .card {
      border: none; }
      .blog-list__container .card-margin .card .details {
        color: rgba(0, 0, 0, 0.54);
        fill: rgba(0, 0, 0, 0.54);
        font-size: 16px; }
  .blog-list__container .card-title {
    font-weight: 600;
    font-size: 20px; }

@media only screen and (max-width: 500px) {
  .blog-list__container {
    margin-top: 30px; }
    .blog-list__container .heading-text {
      font-size: 3.5rem;
      font-weight: 300;
      line-height: 1.2;
      padding-left: 0.2em;
      text-align: center; }
    .blog-list__container .advertise {
      background-color: white; }
    .blog-list__container .lead {
      font-size: 1rem;
      padding-left: 0em;
      text-align: center; } }
